const announcements = (state = initialState, action) => {
  switch(action.type) {
    case SET_ANNOUNCEMENTS:
      return {
        ...state,
        announcementsList: action.payload
      }
    case SET_ANNOUNCEMENT_BY_ID:
      return {
        ...state,
        announcementById: state.test.filter(item => item.id === action.payload)
      }
    case SET_SIGNED:
      return {
        ...state,
        signedIn: action.payload
      }

    default:
      return state
  }
}

export default announcements;
